import React, { useState, useEffect } from 'react'
import ContactList from './components/ContactList'
import Filter from './components/Filter'
import Notification from './components/Notification'
import personService from './services/persons'

const App = () => {

  const [ persons, setPersons] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setFilter ] = useState('')
  const [ notification, setNotification ] = useState('')
  const [ manner, setManner ] = useState('')

  useEffect(() => {
    console.log('effect')
    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
      })
  }, [])
  console.log('render', persons.length, 'persons')

  const addPerson = (event) => {

      event.preventDefault()

      const personObject = {
        name: newName,
        number: newNumber
      }

      const existing = persons.find(p => p.name === newName)

      if (existing !== undefined) {
        if(window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
          
          personService
            .update(existing.id, personObject)
            .then(returnedPerson => {
              setPersons(persons.map(p => p.id !== existing.id ? p : returnedPerson))
            })
            .catch(error => {
              setManner("error")
              setNotification(`${personObject.name} was already deleted from server.`, manner)
                setTimeout(() => {
                setNotification(null)
              }, 5000)
              setPersons(persons.filter(p => p.id !== existing.id))
            })
          setNewName('')
          setNewNumber('')
          setManner('notification')
          setNotification(`${personObject.name}'s number successfully updated to the phonebook.`, manner)
          setTimeout(() => {
            setNotification(null)
          }, 5000)
        }
      }
      else {
        personService
          .create(personObject)
          .then(returnedPerson => {
            setPersons(persons.concat(returnedPerson))
            setNewName('')
            setNewNumber('')
            setManner('notification')
            setNotification(`${personObject.name} successfully added to the phonebook.`, manner)
            setTimeout(() => {
              setNotification(null)
            }, 5000)
          })
          .catch(error => {
            console.log(error.response.data)
            setManner("error")
            setNotification(error.response.data.error, manner)
              setTimeout(() => {
              setNotification(null)
            }, 5000)
          })
        console.log(persons)

      }
    
  }
  
  const handleNameChange = (event) => {
    console.log(event.target.value)
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    console.log(event.target.value)
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    console.log(event.target.value)
    setFilter(event.target.value)
  }

  const getPersons = () => {
    let copy = [...persons]
    
    return copy.filter(person => person.name.toLowerCase().includes(filter))
  }

  const removePerson = (event) => {
    const id = event.target.getAttribute("id")
    const name = event.target.getAttribute("name")
    if(window.confirm(`Do you really want to delete ${name}?`))
    {
      personService
        .remove(id)
      setPersons(persons.filter(p => p.name !== name))
      setManner('notification')
        setNotification(`${name} successfully removed from the phonebook.`, manner)
        setTimeout(() => {
          setNotification(null)
        }, 5000)
    }
  }


  return (
    <div>
      <h1>Phonebook</h1>
      <Notification message={notification} manner={manner}/>
      <Filter value={filter} handler={handleFilterChange}/>
      <h2>Add</h2>
      <div>
          
            <form onSubmit={addPerson}>
                <div>
                    name: <input value={newName} onChange={handleNameChange}/>
                </div>
                <div>
                    number: <input value={newNumber} onChange={handleNumberChange}/>
                </div>
                <div>
                    <button type="submit">Add</button>
                </div>
            </form>

            

        </div>
     
        
        <ContactList persons={getPersons()} removePerson={removePerson}/>
      
  
    </div>
  )

}

export default App



